import { createRouter, createWebHistory } from 'vue-router'

const routes = [
  {
    path: '/',
    redirect: '/app'
  },
  {
    path: '/app',
    name: 'Home',
    meta: {layout: 'main'},
    component: () => import('../views/Home.vue')
  },
  {
    path: '/app/products',
    name: 'Products',
    meta: {layout: 'main'},
    component: () => import('../views/Products.vue')
  },
  {
    path: '/app/orders',
    name: 'Orders',
    meta: {layout: 'main'},
    component: () => import('../views/Orders.vue')
  },
  {
    path: '/app/categories',
    name: 'Categories',
    meta: {layout: 'main'},
    component: () => import('../views/Categories.vue')
  },
  {
    path: '/app/ivent',
    name: 'Ivent',
    meta: {layout: 'main'},
    component: () => import('../views/Ivent.vue')
  },
  {
    path: '/app/comand',
    name: 'Comand',
    meta: {layout: 'main'},
    component: () => import('../views/Comand.vue')
  },
  {
    path: '/app/give',
    name: 'Give',
    meta: {layout: 'main'},
    component: () => import('../views/Give.vue')
  },
  {
    path: '/app/deliveries',
    name: 'Deliveries',
    meta: {layout: 'main'},
    component: () => import('../views/Deliveries.vue')
  },
  {
    path: '/settings',
    name: 'Settings',
    meta: {layout: 'main'},
    component: () => import('../views/Settings.vue')
  },
  {
    path: '/auth/login',
    name: 'Login',
    meta: {layout: 'auth'},
    component: () => import('../views/auth/Login.vue')
  },
  {
    path: '/auth/register',
    name: 'Register',
    meta: {layout: 'auth'},
    component: () => import('../views/auth/Register.vue')
  },
  {
    path: '/auth/reset-password',
    name: 'Reset Password',
    meta: {layout: 'auth'},
    component: () => import('../views/auth/ResetPassword.vue')
  },
  {
    path: '/account/profile',
    name: 'Profile',
    meta: {layout: 'main'},
    component: () => import('../views/account/Profile.vue')
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
